<script lang="ts">
  import type { Repeat, Week } from './lib/defaultData';
  import CategoryList from './components/CategoryList.svelte';
  import { formatDate, week } from './lib/formatDate';
  import { saveStorage, loadStorage, clearStorage } from './lib/storage';
  import Sidebar from './components/Sidebar.svelte';
  import ModeSwitcher from './components/ModeSwitcher.svelte';

  window.onerror = (message, url, line, col, error) => {
    console.log(message);
  };

  let clock = '';
  let data = loadStorage();
  let currentView = NaN;

  const resetDate = new Set<string>();
  let listElement: HTMLElement;

  const nextResetDateClac = (currentDate: Date, repeat: Repeat, repeatTime: string, repeatWeek: Week) => {
    if (repeat == 'daily') {
      const [h, m] = repeatTime.split(':');
      const nextReset = new Date();
      nextReset.setHours(Number(h), Number(m), 0, 0);
      if (nextReset < currentDate) {
        nextReset.setDate(nextReset.getDate() + 1);
      }
      return nextReset.toISOString();
    } else if (repeat == 'weekly') {
      const w = week.findIndex((w) => w === repeatWeek);
      const [h, m] = repeatTime.split(':');
      const nextReset = new Date();
      nextReset.setHours(Number(h), Number(m), 0, 0);
      let addDay = 0;
      if (w - currentDate.getDay() < 0) {
        addDay = w - currentDate.getDay() + 7;
      } else if (w - currentDate.getDay() > 0) {
        addDay = w - currentDate.getDay();
      } else {
        if (nextReset < currentDate) {
          nextReset.setDate(nextReset.getDate() + 7);
        }
      }
      nextReset.setDate(nextReset.getDate() + addDay);

      return nextReset.toISOString();
    }
  };
  const changCurrentView = (event: CustomEvent<number>) => {
    currentView = event.detail;
  };

  const setResetDate = () => {
    const currentDate = new Date();

    const todos = data.todos.flatMap((o) => o.tasks);
    let needsSave = false;
    todos.forEach((t) => {
      try {
        if (t.repeat == 'daily') {
          if (t.checked && t.nextResetDate) {
            if (currentDate >= new Date(t.nextResetDate)) {
              t.checkedDate = '';
              t.checked = false;
              t.nextResetDate = '';
              needsSave = true;
            }
          }
          if (t.nextResetDate) {
            resetDate.add(t.nextResetDate);
          }
        } else if (t.repeat == 'weekly') {
          if (t.checked && t.checkedDate) {
            if (currentDate >= new Date(t.nextResetDate)) {
              t.checkedDate = '';
              t.checked = false;
              t.nextResetDate = '';
              needsSave = true;
            }
          }
          if (t.nextResetDate) {
            resetDate.add(t.nextResetDate);
          }
        }
      } catch (e) {
        if (e instanceof TypeError) {
          clearStorage();
        }
      }
    });

    data = { ...data };
    if (needsSave) {
      saveStorage(data);
    }
  };

  const onClick = (event: any) => {
    const t = event.detail.target;
    if (t.checked) {
      const currentDate = new Date();
      t.checkedDate = currentDate.toISOString();
      t.nextResetDate = nextResetDateClac(currentDate, t.repeat, t.repeatTime, t.repeatWeek);
      resetDate.add(t.nextResetDate);
    } else {
      resetDate.delete(t.nextResetDate);
      t.checkedDate = '';
      t.nextResetDate = '';
    }
    data = { ...data };
    saveStorage(data);
  };

  const interval = () => {
    const cDate = new Date();
    clock = formatDate(cDate, 'YYYY/MM/DD (d) hh:mm:ss');
    cDate.setSeconds(0, 0);
    let forceRefresh = false;
    resetDate.forEach((reset) => {
      if (new Date(reset) <= cDate) {
        resetDate.delete(reset);
        forceRefresh = true;
      }
    });
    if (forceRefresh) {
      setResetDate();
    }
    setTimeout(interval, 1000);
  };

  setResetDate();
  interval();
  let viewType = true;
  let drawer = false;
  let drawer2 = false;
</script>

<div class="h-screen flex">
  <div
    on:click={() => (drawer = !drawer)}
    class="{drawer ? 'block' : 'hidden'} fixed inset-0 bg-black opacity-50 lg:hidden"
  />
  <div on:click={() => (drawer2 = !drawer2)} class="{drawer2 ? 'block' : 'hidden'} fixed inset-0 bg-black opacity-50" />
  <Sidebar bind:drawer bind:clock list={data.todos} bind:current={currentView} on:changCurrentView={changCurrentView} />
  <div class="flex-1 min-w-0 flex flex-col bg-white dark:bg-gray-600 transition duration-500 ease-in-out">
    <div class="px-6">
      <div class="flex-shrink-0 border-b border-gray-200 dark:border-gray-500">
        <header>
          <div class="flex items-center justify-between py-2">
            <div class="flex items-center justify-between">
              <button on:click={() => (drawer = !drawer)} class="text-gray-600 dark:text-gray-300 lg:hidden mr-2"
                ><svg class="h-5 w-5 fill-current" viewBox="0 0 24 24"
                  ><path d="M3 6h18v2H3V6m0 5h18v2H3v-2m0 5h18v2H3v-2z" /></svg
                ></button
              >

              <div class="sm:flex sm:items-center">
                <h2 class="text-2xl font-semibold text-gray-900 dark:text-gray-300 leading-tight">Tasks</h2>
              </div>
            </div>
            <div class="flex">
              {#if false}
                <span
                  class="hidden sm:inline-flex p-1 border bg-gray-200 rounded-md dark:bg-gray-400 dark:border-gray-400"
                  ><button
                    on:click={() => (viewType = true)}
                    class="px-2 py-1 rounded {viewType ? 'bg-white shadow dark:bg-gray-800' : ''}"
                    ><svg class="h-5 w-5 fill-current" viewBox="0 0 24 24"
                      ><path d="M4 5v2h17V5M4 11h17V9H4m0 10h17v-2H4m0-2h17v-2H4v2z" /></svg
                    ></button
                  >
                  <button
                    on:click={() => (viewType = false)}
                    class="px-2 py-1 rounded {viewType ? '' : 'bg-white shadow dark:bg-gray-800'}"
                    ><svg class="h-5 w-5 fill-current" viewBox="0 0 24 24"
                      ><path d="M4 5v13h17V5H4m10 2v9h-3V7h3M6 7h3v9H6V7m13 9h-3V7h3v9z" /></svg
                    ></button
                  ></span
                >
                <button
                  on:click={() => (drawer2 = !drawer2)}
                  class="ml-5 flex items-center pl-2 pr-4 py-2 text-sm font-medium text-white bg-gray-800 rounded-md"
                  ><svg class="h-5 w-5 fill-current" viewBox="0 0 24 24"
                    ><path d="M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z" /></svg
                  > <span class="ml-1">New Task</span></button
                >
              {/if}
              <div class="flex justify-center ml-5 items-center">
                <ModeSwitcher mode={3} />
              </div>
            </div>
          </div>
        </header>
      </div>
      <div class="flex-1 overflow-auto">
        <main class="p-3 h-full overflow-hidden">
          <div bind:this={listElement} class="flex-shrink">
            {#each data.todos as todo, i (todo.id ?? i)}
              {#if Number.isNaN(currentView) || currentView === todo.id}
                <CategoryList data={todo} on:click={onClick} />
              {/if}
            {/each}
          </div>
        </main>
      </div>
    </div>
  </div>
</div>
<div class={drawer2 ? 'fixed top-0 left-0 right-0 bottom-0' : 'hidden'}>
  <div class="border bg-white w-11/12 md:max-w-md rounded shadow-lg z-50 overflow-y-auto">
    <div class="modal-content py-4 text-left px-6">
      <!--Title-->
      <div class="flex justify-between items-center pb-3">
        <p class="text-2xl font-bold">Header</p>
        <div class="modal-close cursor-pointer z-50" on:click={() => (drawer2 = false)}>
          <svg
            class="fill-current text-black"
            xmlns="http://www.w3.org/2000/svg"
            width="18"
            height="18"
            viewBox="0 0 18 18"
          >
            <path
              d="M14.53 4.53l-1.06-1.06L9 7.94 4.53 3.47 3.47 4.53 7.94 9l-4.47 4.47 1.06 1.06L9 10.06l4.47 4.47 1.06-1.06L10.06 9z"
            />
          </svg>
        </div>
      </div>
      <!--Body-->
      <div class="my-5">
        <p>
          Inliberali Persius Multi iustitia pronuntiaret expeteretur sanos didicisset laus angusti ferrentur arbitrium
          arbitramur huic desiderent.?
        </p>
      </div>
      <!--Footer-->
      <div class="flex justify-end pt-2">
        <button class="focus:outline-none modal-close px-4 bg-gray-400 p-3 rounded-lg text-black hover:bg-gray-300"
          >Cancel</button
        >
        <button class="focus:outline-none px-4 bg-teal-500 p-3 ml-3 rounded-lg text-white hover:bg-teal-400"
          >Confirm</button
        >
      </div>
    </div>
  </div>
</div>
{#if false}
  <div class="max-w-screen-xl mx-auto md:items-center md:justify-between md:flex-row md:px-6 lg:px-8">
    <h1 class="bg-blue-100 font-bold text-2xl ring-1 rounded-xl text-center m-auto shadow-inner">ToDo</h1>
    <div class="mt-2">
      <div>{clock}</div>
    </div>
    <div class="flex min-h-screen w-screen">
      <Sidebar list={data.todos} bind:current={currentView} on:changCurrentView={changCurrentView} />
      <div class="flex-grow" bind:this={listElement}>
        {#each data.todos as todo, i (todo.id ?? i)}
          {#if Number.isNaN(currentView) || currentView === todo.id}
            <CategoryList data={todo} on:click={onClick} />
          {/if}
        {/each}
      </div>
    </div>
  </div>
{/if}
