<script lang="ts">
  import { push, link } from 'svelte-spa-router';
  import { onMount } from 'svelte';
  import { saveStorage, loadStorage, clearStorage } from './lib/storage';
  import Sortable from 'sortablejs';

  window.onerror = (message, file, lineNo, colNo, error) => {
    clearStorage();
  };

  export let params: any = {};

  let data = loadStorage();

  let selected = data.todos.length < 1 ? -1 : 0;
  const paramsId = parseInt(params.id, 10);
  if (!isNaN(paramsId)) {
    if (data.todos.length > paramsId) {
      selected = paramsId;
    }
  }

  let listElement: HTMLElement;
  let childListElement: HTMLElement[] = [];

  function moveAt<T>(array_: T[], index: number, at: number): T[] {
    const array = array_.slice();
    if (index === at || index > array.length - 1 || at > array.length - 1) {
      return array;
    }

    const value = array[index];
    const tail = array.slice(index + 1);
    array.splice(index);
    Array.prototype.push.apply(array, tail);
    array.splice(at, 0, value);

    return array;
  }

  onMount(async () => {
    Sortable.create(listElement, {
      animation: 300,
      handle: '.move-handle',
      ghostClass: 'bg-blue-100',
      onUpdate: (evt) => {
        if (evt.oldIndex != evt.newIndex) {
          data.todos = [...moveAt(data.todos, evt.oldIndex, evt.newIndex)];
          saveStorage(data);
        }
      },
    });

    childListElement.forEach((v, i) => {
      Sortable.create(v, {
        animation: 300,
        handle: '.move-handle',
        ghostClass: 'bg-blue-100',
        onUpdate: (evt) => {
          if (evt.oldIndex != evt.newIndex) {
            data.todos[i].tasks = [...moveAt(data.todos[i].tasks, evt.oldIndex, evt.newIndex)];
            saveStorage(data);
          }
        },
      });
    });
  });
</script>

<div class="max-w-screen-xl mx-auto md:items-center md:justify-between md:flex-row md:px-6 lg:px-8">
  <ul class="flex text-gray-500 text-sm lg:text-base my-3 rounded bg-gray-200 p-3">
    <li class="inline-flex items-center">
      <a href="/" use:link>Home</a>
      <svg class="h-5 w-auto text-gray-400" fill="currentColor" viewBox="0 0 20 20">
        <path
          fill-rule="evenodd"
          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
          clip-rule="evenodd"
        />
      </svg>
    </li>
    <li class="inline-flex items-center"><span class="text-teal-400">Manager</span></li>
  </ul>

  <h1 class="bg-blue-100 font-bold text-2xl ring-1 rounded-xl text-center m-auto shadow-inner">管理画面</h1>
  <h2 class="bg-green-50 font-bold text-lg my-2 ring-1 rounded-xl text-center m-auto shadow-inner">カテゴリー</h2>
  <div class="flex flex-col mb-3 mx-3" bind:this={listElement}>
    {#each data.todos as todo, i (todo.id ?? i)}
      <div class="flex items-center border border-gray-300 -mb-px first:rounded-t-md last:rounded-b-md">
        <div class="p-2 bg-gray-100 move-handle">::</div>
        <div class="p-2 cursor-pointer w-full" on:click={() => push(`/manager/category/edit/${i}`)}>
          {todo.categoryName}
        </div>
      </div>
    {/each}
  </div>
  <div class="mb-5 mt-2">
    <button
      class="w-full bg-blue-500 text-white font-bold py-2 px-4 rounded hover:bg-blue-600 focus:ring focus:outline-none"
      on:click={() => push('/manager/category/add')}>+ カテゴリーの追加</button
    >
  </div>
  <h2 class="bg-green-50 font-bold text-lg my-2 ring-1 rounded-xl text-center m-auto shadow-inner">タスク</h2>
  <div class=" mx-2">
    <span class="font-bold">カテゴリー：</span>
    <select class="w-full border bg-white rounded px-3 py-2 outline-none mb-2" bind:value={selected}>
      {#each data.todos as todo, i (todo.id ?? i)}
        <option class="py-1" value={i}>{todo.categoryName}</option>
      {/each}
    </select>
  </div>
  {#each data.todos as todo, i (todo.id ?? i)}
    <div bind:this={childListElement[i]} class="flex flex-col mb-3 mx-3 {selected != i ? 'hidden' : ''}">
      {#each todo.tasks as task, l (task.id ?? l)}
        <div class="flex items-center border border-gray-300 -mb-px first:rounded-t-md last:rounded-b-md">
          <div class="p-2 bg-gray-100 move-handle">::</div>
          <div
            class="py-2 pr-2 flex items-center w-full cursor-pointer"
            on:click={() => push(`/manager/task/edit/${i}/${l}`)}
          >
            <input class="mx-1" type="checkbox" bind:checked={task.checked} disabled />
            <div>{task.title}</div>
          </div>
        </div>
      {/each}
    </div>
  {/each}

  <div class="mb-5 mt-2">
    <button
      class="disabled:opacity-50 w-full bg-blue-500 text-white font-bold py-2 px-4 rounded hover:bg-blue-600 focus:ring focus:outline-none"
      on:click={() => push(`/manager/task/add/${selected}`)}
      disabled={selected < 0}>+ 項目の追加</button
    >
  </div>
</div>

<style lang="postcss">
  .move-handle {
    @apply cursor-move;
  }
</style>
