<script lang="ts">
  import { link } from 'svelte-spa-router';
  import { createEventDispatcher } from 'svelte';
  import type { Todo } from '../lib/defaultData';

  export let list: Todo[] = [];
  export let current: number;
  export let clock: string;
  export let drawer: boolean;

  const dispatch = createEventDispatcher<{ changCurrentView: number }>();

  const click = (id: number) => {
    dispatch('changCurrentView', id);
  };
</script>

<div
  class="{drawer
    ? 'block fixed z-30 '
    : 'hidden'} lg:block w-64 px-8 py-4 bg-gray-100 dark:bg-gray-800 overflow-auto h-full transition duration-500 ease-in-out"
>
  <div class="flex flex-row border-b items-center justify-between pb-1">
    <h1 class="text-2xl font-semibold capitalize dark:text-gray-300">Todo</h1>
    <span class="relative"
      ><a
        class="hover:text-green-500 dark-hover:text-green-300 text-gray-600 dark:text-gray-300"
        href="/manager"
        use:link
        ><svg
          class="w-5 h-5 stroke-current"
          viewBox="0 0 24 24"
          fill="none"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
          ><path
            d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z"
          />
          <path d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" /></svg
        ></a
      >
    </span>
  </div>
  <span><span class="text-sm dark:text-gray-300">{clock}</span></span>

  <nav class="mt-8">
    <h3 class="text-xs font-semibold text-gray-600 dark:text-gray-300 uppercase tracking-wide">カテゴリー</h3>

    <div class="mt-2 -mx-3">
      <li
        on:click={() => click(NaN)}
        class="mb-1 flex justify-between items-center px-3 py-2 cursor-pointer {Number.isNaN(current)
          ? 'bg-gray-200  rounded-lg text-gray-900'
          : 'hover:bg-gray-200 rounded-lg text-gray-700 hover:text-gray-900 dark:text-gray-300 dark:hover:text-gray-900'}"
      >
        <span class="text-sm font-medium">すべて</span>
      </li>

      {#each list as c}
        <li
          on:click={() => click(c.id)}
          class="mb-1 flex justify-between items-center px-3 py-2 cursor-pointer {current === c.id
            ? 'bg-gray-200 rounded-lg text-gray-900'
            : 'hover:bg-gray-200 rounded-lg text-gray-700 hover:text-gray-900 dark:text-gray-300 dark:hover:text-gray-900'}"
        >
          <span class="text-sm font-medium">{c.categoryName}</span>
        </li>
      {/each}
    </div>
    {#if false}
      <button
        class="w-full -ml-3 px-3 py-2 flex justify-between items-center text-sm font-medium hover:bg-gray-200 rounded-lg dark:hover:text-gray-900"
      >
        Create New Category
      </button>
    {/if}
  </nav>
</div>

{#if false}
  <aside class="bg-white w-60 h-screen sticky top-0">
    <div class="flex items-center justify-center mt-5">カテゴリー</div>
    <nav class="mt-5">
      <li
        on:click={() => click(NaN)}
        class="flex items-center py-2 px-8 border-r-4 cursor-pointer
        {Number.isNaN(current)
          ? ' bg-gray-200 text-gray-700 border-gray-700'
          : ' text-gray-60 border-white hover:bg-gray-100 hover:text-gray-700 hover:border-gray-700'}"
      >
        すべて
      </li>
      {#each list as c}
        <li
          class="flex items-center py-2 px-8 border-r-4 cursor-pointer
          {current === c.id
            ? ' bg-gray-200 text-gray-700 border-gray-700'
            : ' text-gray-60 border-white hover:bg-gray-100 hover:text-gray-700 hover:border-gray-700'}"
          on:click={() => click(c.id)}
        >
          {c.categoryName}
        </li>
      {/each}
    </nav>
  </aside>
{/if}
