<script lang="ts">
  import type { Todo, Task, Repeat, Week } from './lib/defaultData';
  import { push, link } from 'svelte-spa-router';
  import { saveStorage, loadStorage } from './lib/storage';
  import TaskEditor from './components/TaskEditor.svelte';

  export let params: any = {};
  const catId = parseInt(params.cat, 10);
  if (isNaN(catId)) {
    push('/manager');
  }

  let data = loadStorage();
  if (!data.todos[catId]) {
    push('/manager');
  }
  let title = '';
  let repeat: Repeat = '';
  let repeatTime = '';
  let repeatWeek: Week = '日';

  const onSave = () => {
    const newId = data.todos[catId].tasks.reduce((a, b) => (a > b.id ? a : b.id), 0) + 1;
    const addJson: Task = {
      id: newId,
      title: title,
      checked: false,
      checkedDate: '',
      repeat: repeat,
      repeatTime: repeatTime,
      repeatWeek: repeatWeek,
      nextResetDate: '',
    };
    if (addJson.title) {
      data.todos[catId].tasks.push(addJson);
      saveStorage(data);
    }
    push(`/manager/${catId}`);
  };
</script>

<div class="max-w-screen-xl mx-auto md:items-center md:justify-between md:flex-row md:px-6 lg:px-8">
  <ul class="flex text-gray-500 text-sm lg:text-base my-3 rounded bg-gray-200 p-3">
    <li class="inline-flex items-center">
      <a href="/" use:link>Home</a>
      <svg class="h-5 w-auto text-gray-400" fill="currentColor" viewBox="0 0 20 20">
        <path
          fill-rule="evenodd"
          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
          clip-rule="evenodd"
        />
      </svg>
    </li>
    <li class="inline-flex items-center">
      <a href="/manager" use:link>Manager</a>
      <svg class="h-5 w-auto text-gray-400" fill="currentColor" viewBox="0 0 20 20">
        <path
          fill-rule="evenodd"
          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
          clip-rule="evenodd"
        />
      </svg>
    </li>
    <li class="inline-flex items-center"><span class="text-teal-400">Todo Add</span></li>
  </ul>

  <h1 class="bg-blue-100 font-bold text-2xl ring-1 rounded-xl text-center m-auto shadow-inner">項目の追加</h1>
  <h2 class="mx-3 bg-green-50 font-bold text-lg mt-2 mb-1 ring-1 rounded-xl text-center m-auto shadow-inner">
    {data.todos[catId].categoryName}
  </h2>
  <TaskEditor bind:title bind:repeat bind:repeatTime bind:repeatWeek {onSave} />
</div>
