<script lang="ts">
  import { onMount } from 'svelte';
  let darkMode = false;
  const THEME_KEY = 'themePreference';
  export let mode: number = 1;

  function setDarkTheme(dark: boolean) {
    darkMode = dark;
    document.documentElement.classList.toggle('dark', darkMode);
  }

  function toggleMode() {
    console.log(`toggleMode`);
    setDarkTheme(!darkMode);
    window.localStorage.setItem(THEME_KEY, darkMode ? 'dark' : 'light');
  }

  onMount(() => {
    const theme = window.localStorage.getItem(THEME_KEY);
    if (theme === 'dark') {
      setDarkTheme(true);
    } else if (theme == null && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      setDarkTheme(true);
    }
  });
</script>

{#if mode === 1}
  <div class="absolute top-0 right-0 w-8 h-8 p-2" on:click={toggleMode}>
    {#if darkMode}
      <svg
        aria-hidden="true"
        focusable="false"
        data-prefix="far"
        data-icon="moon"
        class="svg-inline--fa fa-moon fa-w-16"
        role="img"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 512 512"
        ><path
          fill="currentColor"
          d="M279.135 512c78.756 0 150.982-35.804 198.844-94.775 28.27-34.831-2.558-85.722-46.249-77.401-82.348 15.683-158.272-47.268-158.272-130.792 0-48.424 26.06-92.292 67.434-115.836 38.745-22.05 28.999-80.788-15.022-88.919A257.936 257.936 0 0 0 279.135 0c-141.36 0-256 114.575-256 256 0 141.36 114.576 256 256 256zm0-464c12.985 0 25.689 1.201 38.016 3.478-54.76 31.163-91.693 90.042-91.693 157.554 0 113.848 103.641 199.2 215.252 177.944C402.574 433.964 344.366 464 279.135 464c-114.875 0-208-93.125-208-208s93.125-208 208-208z"
        /></svg
      >
    {:else}
      <svg class="w-5 h-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
        />
      </svg>
    {/if}
  </div>
{:else if mode === 2}
  <div class="flex">
    <div class="mr-2 text-xs">
      <svg class="w-6 h-6" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
        />
      </svg>
    </div>
    <div class="relative inline-block w-10 mr-2 align-middle select-none transition duration-200 ease-in">
      <input
        type="checkbox"
        name="toggle"
        id="toggle"
        bind:checked={darkMode}
        class="toggle-checkbox absolute block w-6 h-6 dark:bg-white border-4 dark:border-gray-400 rounded-full appearance-none cursor-pointer focus:outline-none bg-gray-800 border-gray-100"
        on:click={toggleMode}
      />
      <label
        for="toggle"
        class="block h-6 align-middle overflow-hidden text-gray-100 bg-gray-100 rounded-full cursor-pointer dark:text-gray-400 dark:bg-gray-400 dark:border-gray-400 border-gray-100 border-2"
      >
        toggle
      </label>
    </div>
    <div class="text-xs">
      <svg class="w-6 h-6" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
        />
      </svg>
    </div>
  </div>
{:else if mode === 3}
  <div class="flex items-center select-none">
    <button
      on:click={toggleMode}
      class="hover:text-green-500 dark-hover:text-green-300 focus:outline-none cursor-pointer transition duration-500 ease-in-out"
    >
      <svg
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        {#if !darkMode}
          <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z" />
        {:else}
          <circle cx="12" cy="12" r="5" />
          <line x1="12" y1="1" x2="12" y2="3" />
          <line x1="12" y1="21" x2="12" y2="23" />
          <line x1="4.22" y1="4.22" x2="5.64" y2="5.64" />
          <line x1="18.36" y1="18.36" x2="19.78" y2="19.78" />
          <line x1="1" y1="12" x2="3" y2="12" />
          <line x1="21" y1="12" x2="23" y2="12" />
          <line x1="4.22" y1="19.78" x2="5.64" y2="18.36" />
          <line x1="18.36" y1="5.64" x2="19.78" y2="4.22" />
        {/if}
      </svg>
    </button>
  </div>
{/if}

<style lang="postcss">
  .toggle-checkbox:checked {
    @apply right-0 border-gray-400;
  }
</style>
