<script lang="ts">
  import type { Task } from '../lib/defaultData';
  import { createEventDispatcher } from 'svelte';
  import { formatDate } from '../lib/formatDate';

  export let task: Task;

  const dispatch = createEventDispatcher();
  const onClick = (target: Task, checkbox: boolean) => {
    console.log(`click`);
    if (!checkbox) task.checked = !task.checked;
    dispatch('click', {
      target: target,
    });
  };
</script>

<div
  class="flex items-start border-t p-1 border-gray-300 cursor-pointer content-center"
  on:click|preventDefault={() => onClick(task, false)}
>
  <div class="items-center content-center">
    <div class="inline-block">
      <div class="relative">
        <svg
          class="stroke-current text-blue-500 dark:text-blue-200 w-4 h-4 absolute top-2 left-0.5 {!task.checked
            ? 'hidden'
            : 'inline-block'}"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 20 20"><polyline fill="none" stroke-width="3" points="2,7.3 7.3,12.7 18,2" /></svg
        >
      </div>
      <input
        type="checkbox"
        class="appearance-none pointer-events-none relative cursor-pointer m-0 p-0 inline-block align-middle whitespace-normal top-0 left-0 w-5 h-5 border border-gray-300 rounded"
        checked={task.checked}
      />
    </div>
    <div class="inline-block">
      {task.title}
      {#if task.nextResetDate}
        <span class="text-gray-500 dark:text-gray-300"
          >(次回リセット→{formatDate(new Date(task.nextResetDate), 'YYYY/MM/DD (d) hh:mm')})</span
        >
      {/if}
    </div>
  </div>
</div>
