<script lang="ts">
  import type { Todo, Task, Repeat, Week } from '../lib/defaultData';
  import { week } from '../lib/formatDate';

  export let title = '';
  export let repeat: Repeat;
  export let repeatTime: string;
  export let repeatWeek: Week;
  export let onSave = () => {};
  export let onDelete: Function = null;

  let showModal = false;
  const deleteConfirm = () => {
    showModal = true;
  };
  const onCancel = () => {
    showModal = false;
  };

  let isValid = false;

  function validate(node: any, binding: any) {
    function excute() {
      let error = false;
      if (!title) error = true;
      if (!['no', 'daily', 'weekly'].includes(repeat)) error = true;
      if (['daily', 'weekly'].includes(repeat) && !repeatTime) error = true;
      if (repeat == 'weekly' && !week.includes(repeatWeek)) error = true;

      isValid = !error;
    }
    excute();

    return {
      update(value: any) {
        excute();
      },
    };
  }

  const onValidate = () => {
    if (isValid) onSave();
  };
</script>

<div class="bg-gray-100 shadow-lg rounded px-12 pt-6 pb-8 my-4 mx-3">
  <input
    class="shadow appearance-none border rounded w-full py-2 px-3 mb-3 text-gray-700 leading-tight focus:outline-none focus:ring"
    type="text"
    use:validate={title}
    bind:value={title}
  />

  <div>
    <label for="country" class="block text-sm font-medium text-gray-700">繰り返し</label>

    <div class="mt-1 flex">
      <div class="flex items-center mr-5">
        <input
          id="radio_no"
          name="radio_repeat"
          type="radio"
          value="no"
          use:validate={repeat}
          bind:group={repeat}
          class="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300"
        />
        <label for="radio_no" class="ml-1 block text-sm font-medium text-gray-700">なし</label>
      </div>
      <div class="flex items-center mr-5">
        <input
          id="radio_daily"
          name="radio_repeat"
          type="radio"
          value="daily"
          use:validate={repeat}
          bind:group={repeat}
          class="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300"
        />
        <label for="radio_daily" class="ml-1 block text-sm font-medium text-gray-700">デイリー</label>
      </div>
      <div class="flex items-center">
        <input
          id="radio_weekly"
          name="radio_repeat"
          type="radio"
          value="weekly"
          use:validate={repeat}
          bind:group={repeat}
          class="focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300"
        />
        <label for="radio_weekly" class="ml-1 block text-sm font-medium text-gray-700">ウイークリー</label>
      </div>
    </div>
  </div>

  {#if repeat === 'daily' || repeat === 'weekly'}
    <div class="mt-2">
      <label for="country" class="block text-sm font-medium text-gray-700">リセット時間</label>

      {#if repeat == 'weekly'}
        <select
          id="country"
          name="country"
          autocomplete="country"
          use:validate={repeatWeek}
          bind:value={repeatWeek}
          class="inline-flex w-auto h-19 py-2 px-3 border border-gray-300 bg-white rounded-md shadow-lg focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 ">
          {#each week as w}
            <option value={w}>{w}</option>
          {/each}
        </select>
      {/if}

      <input
        type="time"
        use:validate={repeatTime}
        bind:value={repeatTime}
        class="inline-flex w-auto h-19 py-2 px-3 border border-gray-300 bg-white rounded-md shadow-lg focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
      />
    </div>
  {/if}

  <div class="flex justify-between mt-3">
    <div>
      <button
        class="disabled:opacity-50 px-4 py-2 rounded text-white inline-block shadow-lg bg-blue-500 hover:bg-blue-600 focus:bg-blue-700 focus:outline-none focus:ring"
        on:click={onValidate}
        disabled={!isValid}>保存</button
      >
    </div>
    {#if onDelete}
      <div>
        <button
          on:click={deleteConfirm}
          class="disabled:opacity-50 px-4 py-2 rounded text-red-500 inline-block shadow-lg border border-red-500 hover:border-red-600 focus:border-red-700 focus:outline-none focus:ring"
          >削除</button
        >
      </div>
    {/if}
  </div>
  {#if showModal}
    <div class="fixed z-10 inset-0 overflow-y-auto">
      <div class="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div class="fixed inset-0 transition-opacity" aria-hidden="true">
          <div class="absolute inset-0 bg-gray-500 opacity-75" />
        </div>

        <span class="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
        <div
          class="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full"
          role="dialog"
          aria-modal="true"
          aria-labelledby="modal-headline"
        >
          <div class="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
            <div class="sm:flex sm:items-start">
              <div
                class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10"
              >
                <svg
                  class="h-6 w-6 text-red-600"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                  aria-hidden="true">
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                  />
                </svg>
              </div>
              <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                <h3 class="text-lg leading-6 font-medium text-gray-900" id="modal-headline">Delete todo</h3>
                <div class="mt-2">
                  <p class="text-sm text-gray-500">この項目を削除しますか？</p>
                </div>
              </div>
            </div>
          </div>
          <div class="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
            <button
              type="button"
              on:click={() => onDelete()}
              class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm">
              Delete
            </button>
            <button
              type="button"
              on:click={onCancel}
              class="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm">
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  {/if}
</div>
